/**
 * @file
 * Styles for administration pages.
 */
/**
 * Node add/edit form layout
 */
/* Narrow screens */
.layout-region {
  box-sizing: border-box; }

/* Wide screens */
/* stylelint-disable-next-line */
@media screen and (min-width: 780px), (orientation: landscape) and (min-device-height: 780px) {
  .layout-region-main,
  .layout-region-footer {
    float: left;
    /* LTR */
    box-sizing: border-box;
    width: 65%;
    padding-right: 2em;
    /* LTR */ }
  [dir='rtl'] .layout-region-main,
  [dir='rtl'] .layout-region-footer {
    float: right;
    padding-right: 0;
    padding-left: 2em; }
  .layout-region-secondary {
    float: right;
    /* LTR */
    width: 35%; }
  [dir='rtl'] .layout-region-secondary {
    float: left; }
  /* @todo File an issue to add a standard class to all text-like inputs */
  .layout-region-secondary .form-autocomplete,
  .layout-region-secondary .form-text,
  .layout-region-secondary .form-tel,
  .layout-region-secondary .form-email,
  .layout-region-secondary .form-url,
  .layout-region-secondary .form-search,
  .layout-region-secondary .form-number,
  .layout-region-secondary .form-color,
  .layout-region-secondary textarea {
    box-sizing: border-box;
    width: 100%;
    max-width: 100%; } }

/**
 * The vertical toolbar mode gets triggered for narrow screens, which throws off
 * the intent of media queries written for the viewport width. When the vertical
 * toolbar is on, we need to suppress layout for the original media width + the
 * toolbar width (240px). In this case, 240px + 780px.
 */
/* stylelint-disable-next-line */
@media screen and (max-width: 1020px) {
  .toolbar-vertical.toolbar-tray-open .layout-region-main,
  .toolbar-vertical.toolbar-tray-open .layout-region-footer,
  .toolbar-vertical.toolbar-tray-open .layout-region-secondary {
    float: none;
    width: auto;
    padding-right: 0; } }

.field--widget-plugin-reference-options-buttons .form-checkboxes {
  display: flex; }

.field--widget-plugin-reference-options-buttons .form-checkboxes .form-type-checkbox {
  padding: 0 0.5rem; }
